body {
  height: 100vh;
  background-color: #f2f2f2;
  justify-content: center;
  align-items: center;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.container {
  text-align: center;
  width: 500px;
  background-color: #fff;
  border-radius: 10px;
  padding: 40px;
  position: relative;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.input {
  margin-bottom: 15px;
}

.process-btn {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 5px;
  margin-top: 20px;
  padding: 10px 20px;
  font-size: 16px;
}

.process-btn:hover {
  background-color: #45a049;
}

.text {
  margin: 10px 0;
  font-size: 18px;
}

.text-value {
  color: #333;
  font-weight: bold;
}

.loader {
  width: 50px;
  height: 50px;
  border: 8px solid #f3f3f3;
  border-top-color: #4caf50;
  border-radius: 50%;
  animation: spin 2s linear infinite;
  display: none;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

